@import 'assets/scss/utils/mixins';

.signup-container {
	border: 1px solid var(--general-2);
	border-radius: 4px;
}

.horizontal-line {
	width: 100px;
	height: 5px;
	opacity: 15%;
	background-color: var(--prim-2);
	border-radius: 100px;
}

.check-circle {
	background: rgba(255, 90, 184, 0.149);
}

