.badge-text {
	padding-left: 8px;
	padding-right: 8px;
	background-color: var(--general-5);
	color: var(--basic-1);
	border-radius: 4px;
	font-size: 12px;
	font-weight: 500;
}

.full-width-table {
	width: 100%;
	border-collapse: collapse;
}

table {
	td,
	th {
		border: 1px solid var(--neutral-2) !important; /* Light grey border */
		padding: 16px 12px 16px 16px !important;
	}

	th.subscription-offers,
	td.subscription-offers {
		max-width: 638px;
		word-wrap: break-word;
	}

	td.subscription-offers > findqo-subscription-offers {
		display: block;
		margin-right: 180px;
	}
}
